<template>
  <div>
    <q-banner
      inline-actions
      rounded
      class="text-white"
      style="margin-top: 30px; background-color: #000"
    >
      <q-btn>
        <img style="height: 32px; width: 32px" :src="lang_type_icon" />
      </q-btn>
      {{ item_title }}

      <template v-slot:action>
        <q-btn flat label="Details" />
      </template>
      <q-badge align="top">New</q-badge>
    </q-banner>
    <q-card style="margin-bottom: 50px">
      <q-splitter v-model="splitterModel" style="height: 150px">
        <template v-slot:before>
          <q-tabs v-model="tab" vertical class="text-black">
            <q-tab name="technology" icon="construction" label="Technology" />
            <q-tab name="description" icon="pages" label="Description" />
          </q-tabs>
        </template>

        <template v-slot:after>
          <q-tab-panels
            v-model="tab"
            animated
            swipeable
            vertical
            transition-prev="jump-up"
            transition-next="jump-up"
          >
            <q-tab-panel name="technology">
              <div class="text-h4 q-mb-md">{{ technology_title }}</div>
              <p>
                {{ technology_context }}
              </p>
            </q-tab-panel>

            <q-tab-panel name="description">
              <div class="text-h4 q-mb-md">{{ description_title }}</div>
              <p>
                {{ description_context }}
              </p>
            </q-tab-panel>
          </q-tab-panels>
        </template>
      </q-splitter>
    </q-card>
  </div>
</template>

<script>
import { ref } from "vue";

export default {
  data() {
    return {
      item_title: "JAVA ",
      lang_type_icon: require("@/assets/logo/java.svg"),
      technology_title: "technology_title",
      technology_context: "technology_context",
      description_title: "description_title",
      description_context: "description_context",
    };
  },

  setup() {
    return {
      tab: ref("technology"),
      splitterModel: ref(20),
    };
  },
};
</script>
